import React, { useRef, useEffect, useState } from "react";

type Props = {
  children: React.ReactNode;
  threshold?: number; // ÏñºÎßàÎÇò Î≥¥Ïó¨Ïïº Îì±Ïû•Ìï†ÏßÄ (Í∏∞Î≥∏: 0.1)
  delay?: number;     // Ïï†ÎãàÎ©îÏù¥ÏÖò ÏßÄÏó∞ ÏãúÍ∞Ñ (ms)
};

export function FadeInSection({ children, threshold = 0.1, delay = 0 }: Props) {
  const ref = useRef<HTMLDivElement | null>(null);
  const [isVisible, setVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setVisible(true);
          observer.disconnect(); // Ìïú Î≤àÎßå Ïã§Ìñâ (Ïû¨Îì±Ïû• Î∞©ÏßÄ Ïãú Ï†úÍ±∞ Í∞ÄÎä•)
        }
      },
      { threshold }
    );

    if (ref.current) observer.observe(ref.current);

    return () => observer.disconnect();
  }, [threshold]);

  return (
    <div
      ref={ref}
      style={{
        opacity: isVisible ? 1 : 0,
        transform: isVisible ? "translateY(0px)" : "translateY(30px)",
        transition: `opacity 0.8s ease-out ${delay}ms, transform 0.8s ease-out ${delay}ms`,
        willChange: "opacity, transform",
      }}
    >
      {children}
    </div>
  );
}
