  border: "none",
  backgroundColor: "var(--white)",
  boxShadow: "0 5px 15px rgba(0, 0, 0, 0.1)",
  cursor: "pointer",
  transition: "var(--transition)",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  
